<template>
  <div id="searchLocation_container_wrapper">
    <div id="mapContainer"></div>
    <input type="text" id="keyword" name="keyword" v-model.trim="keyword" placeholder="请输入关键字：(选定后搜索)"
    />
  </div>
</template>

<script>
export default {
  name: 'searchLocation',
  data() {
    return {
      windowsArr: [],
      marker: [],
      keyword: '',
      map: null,
      autocomplete: null,
    };
  },
  watch: {
    keyword() {
      console.info(11);
      this.searchData();
    },
  },
  methods: {
    searchData() {
      this.map.plugin(['AMap.Autocomplete', 'AMap.PlaceSearch'], function() {
        var autoOptions = {
          city: '北京', //城市，默认全国
          input: 'keyword', //使用联想输入的input的id
        };
        this.autocomplete = new AMap.Autocomplete(autoOptions);
        var placeSearch = new AMap.PlaceSearch({
          city: '北京',
          map: this.map,
        });
        this.map.event.addListener(this.autocomplete, 'select', function(e) {
          console.info('返回的值', e);
          //TODO 针对选中的poi实现自己的功能
          placeSearch.setCity(e.poi.adcode);
          placeSearch.search(e.poi.name);
        });
      });
    },
  },
  mounted() {
    this.map = new AMap.Map('mapContainer', {
      resizeEnable: true,
      center: [116.397428, 39.90923], //地图中心点
      zoom: 13, //地图显示的缩放级别
      keyboardEnable: false,
    });
  },
};
</script>

<style lang="scss">
#searchLocation_container_wrapper {
  height: 100vh;
  width: 100%;
  position: relative;
  #mapContainer {
    width: 100%;
    height: 100%;
  }
  #keyword {
    position: absolute;
    top: 0.3rem;
    width: 100%;
    height: 1rem;
    border: 0;
    padding-left: 0.3rem;
    border-radius: 0.5rem;
    outline: none;
    border: 1px solid #dfdfdf;
  }
}
</style>



